.App {
  text-align: center;
}

.bbq-logo {
  height: 150px;
  vertical-align: middle;
}

.row {
  display: inline-block;
  vertical-align: middle;
}

.App-header {
  background-color: #222;
  background-image: url("./assets/img/bonfire.png");
  background-repeat: repeat-x;
  background-position: bottom;
  background-size: 46px;
  height: 170px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
  min-height: 50vh;
  word-wrap: break-word;
}

.App-footer {
  display: block;
  font-size: 13px;
}

.App-button {
  background: #d6d934;
  background-image: -webkit-linear-gradient(top, #d6d934, #b8362b);
  background-image: -o-linear-gradient(top, #d6d934, #b8362b);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#d6d934), to(#b8362b));
  background-image: linear-gradient(to bottom, #d6d934, #b8362b);
  border-radius: 28px;
  font-family: Arial;
  color: #ffffff;
  font-size: 16px;
  padding: 10px 10px 10px 10px;
  text-decoration: none;
  outline:none;
}

.App-button:hover {
  background: #f44336;
  background-image: -webkit-linear-gradient(top, #f44336, #ffeb3b);
  background-image: -o-linear-gradient(top, #f44336, #ffeb3b);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#f44336), to(#ffeb3b));
  background-image: linear-gradient(to bottom, #f44336, #ffeb3b);
  text-decoration: none;
}

.App-grill {
  display: inline-block;
  position: relative;
}
.App-items {
  padding-top: 10px;
  width: 250px;
}
@media only screen and (max-width: 612px) {
  .App-items {
    width: 200px;
    margin-left: initial !important;
    margin-top: 180px;
  }
}
.App-items img {
  width: 100px;
}
.App-items h3 {
  margin: 0;
  padding: 0;
}
.App-items ul {
  list-style: none;
  padding: 0;
}
.App-items ul li {
  padding: 10px;
}
.App-items ul li p {
  padding-left: 20px;
  display: block;
}

.App-grill span, .App-grill .grill {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center; 
  border: 1px dashed #ff0000;
}
.App-items-steak {
  background-image: url('./assets/svg/steak.svg');
}
.App-items .App-items-steak {
  background-position: 9px 3px;
}
.App-items-sausage {
  background-image: url('./assets/svg/sausage.svg');
}
.App-items .App-items-sausage {
  background-size: 50%;
  background-position: center -12px;
}
.App-items-veal {
  background-image: url('./assets/svg/veal.svg');
}
.App-items .App-items-veal {
  background-size: 80%;
}
.App-items-tomato {
  background-image: url('./assets/svg/tomato.svg');
}
.App-grill .grill {
  background-image: url('./assets/svg/grill.svg');
  background-position: 0 10px;
  border: 5px solid #aea8af;
}
.App-footer {
  border-top: 1px dashed #ccc;
}
.App-grill, .App-footer {
  margin-top: 30px;
}
.App-happy {
  background-image: url('./assets/svg/happy.svg');
  width: 32px;
  height: 32px;
}
.App-items .fadeOut{
   opacity:0;
   width:0;
   height:0;
   -webkit-transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
   -o-transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
   transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
}
.App-items .fadeIn{
   opacity:1;
   -webkit-transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
   -o-transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
   transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
}
.ongrill-steak, .ongrill-sausage, .ongrill-tomato, .ongrill-veal {
  position: absolute;
  top: 0;
}
.ongrill-steak1 {
  left: 0;
}
.ongrill-steak2 {
  left: 50px;
}
.ongrill-steak3 {
  left: 100px;
}
.ongrill-steak4 {
  left: 150px;
}
.ongrill-steak5 {
  left: 120px;
  top: 116px;
}
.ongrill-sausage1 {
  left: 0;
  top: 42px;
}
.ongrill-sausage2 {
  left: 80px;
  top: 42px;
}
.ongrill-sausage3 {
  left: 0;
  top: 62px;
}
.ongrill-sausage4 {
  left: 80px;
  top: 62px;
}
.ongrill-tomato1 {
  right: 0;
  top: 42px;
}
.ongrill-tomato2 {
  right: 0;
  top: 72px;
}
.ongrill-tomato3 {
  right: 0;
  top: 104px;
}
.ongrill-veal1 {
  left: 0;
  top: 84px;
}
.ongrill-veal2 {
  left: 40px;
  top: 84px;
}
.ongrill-veal3 {
  left: 80px;
  top: 84px;
}
.ongrill-veal4 {
  left: 120px;
  top: 84px;
}
.ongrill-veal5 {
  left: 0;
  top: 116px;
}
.ongrill-veal6 {
  left: 40px;
  top: 116px;
}
.ongrill-veal7 {
  left: 80px;
  top: 116px;
}






